import React from 'react';
import styled from 'styled-components';
import { Button } from 'antd';
import { Link } from 'react-router-dom';

const ReactDiv = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  position: relative;
  transition: 0.7s all;
  h2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1em;
  }
  article {
    background: white;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 70vh;
    h1 {
      font-size: 2em;
      text-align: center;
      line-height: 2;
      padding: 30px 0 0 0;
    }
  }
  @media screen and (max-width:768px){
      article{
      padding:30px;
      width:90%;
      height:80vh;
      h1{
          font-size:2em;
          line-height:1.7;
          padding:0
      }
      }
  }
`;

function ReactPages() {
    return (
        <div>
            <ReactDiv>
                <article>
                    <Link to="/">
                        <h1>Momentum</h1>
                    </Link>
                    <Link to="reactpages/calculator">
                        <h1>Calculator - Working</h1>
                    </Link>
                    <a href="https://junchi86.github.io/moviev1/" target="_blank" rel="nonrefferer noopener"><h1>Movie App ver.1</h1></a>
                    <a href="https://junchi86.github.io/shop/" target="_blank" rel="nonrefferer noopener"><h1>Cherry Coke</h1></a>
                    <a href="https://junchi86.github.io/mask/" target="_blank" rel="nonrefferer noopener"><h1>MaskMan!!</h1></a>
                    <a href="https://junchi86.github.io/coinExplorer/" target="_blank" rel="nonrefferer noopener"><h1>Coin Explorer</h1></a>



                </article>
                <Link to='/menupage'>
                    <Button
                        style={{
                            fontSize: '2em',
                            lineHeight: '63px',
                            height: '63px',
                            position: 'absolute',
                            top: 0,
                            borderRadius: 0,
                            color: 'tomato',
                        }}
                        block
                        type="normal"
                    >
                        Close
                    </Button>
                </Link>
            </ReactDiv>
        </div>
    )
}

export default ReactPages
