import React from 'react';
import styled from 'styled-components';
import { Icon } from 'antd';
import { Link, useHistory } from 'react-router-dom';


const Div = styled.div`
  position:relative;
  width: 100%;
  height: 100vh;
  background: white;
  font-size: 3em;
  text-align: center;
  transition: 1s all;
  color: white;
  header {
    width: 100%;
    background: black;
    cursor: pointer;
  }
  div {
      position:absolute;
      width:100%;
      top:50%;
      left:50%;
      transform:translate(-50%,-50%);
    }
  h3 {
    margin:auto;
    text-align:center;
    cursor: pointer;
    color:black;
  }
  h3:hover{
    transition: 0.5s all;
    color:tomato
  }
  h3:nth-child(3){
    cursor: unset
  }
  h5{
    margin:auto;
    position:absolute;
    width:100%;
    height:auto;
    bottom:0;
    left:0;
    text-align:center;
    color:tomato;
    background:white;
    cursor: pointer;
    border-top: 1px solid lightgray;
    box-shadow:inset 0px 1px 7px lightgray;
    font-size:1em;
    line-height:2em

  }
  h5:hover{
    background:bisque
  }
  @media only screen and (max-width: 500px) {
    h3 {
      font-size: 0.8em;
    }
    br{
      display:none
    }
    h5 {
      font-size:0.5em;
    }
  }
`

function MenuPage() {
  let history = useHistory()
  const onLogout = () => {
    if (window.confirm('로그아웃하시겠습니까?') === true) {
      localStorage.removeItem('todo');
      localStorage.removeItem('currentUser');
      history.push('/')
    } else {
      return false;
    }
  };

  return (
    <>
      <Div>
        <Link to='/'>
          <header>
            <Icon type="caret-right" />
          </header>
        </Link>
        <div>
          <Link to='/menupage/javascript'><h3>JavaScript</h3></Link>
          <Link to='/menupage/reactpages'><h3>React</h3></Link>
          <Link to='/menupage/node'><h3>Node.js</h3></Link>
          <Link to='/menupage/pub/'><h3>Etc.Pages</h3></Link>
          <br />
        </div>
        {localStorage.getItem('currentUser') ? <h5 onClick={() => onLogout()}>Log Out?</h5> : null}
      </Div>



    </>
  );
}

export default MenuPage;
