import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import Momentum from './momentum/Momentum';
import Calculator from './momentum/components/ReactPages/Calculator/Calculator';
import MenuPage from './momentum/components/SubMenu/MenuPage';
import JavaScript from './momentum/components/SubMenu/Component/JavaScript';
import Node from './momentum/components/SubMenu/Component/Node';
import Pub from './momentum/components/SubMenu/Component/Pub';
import ReactPages from './momentum/components/SubMenu/Component/ReactPages';

function App() {
  return (
    <>
      <Router>
        <Route exact path="/" component={Momentum}></Route>
        <Route exact path="/menupage" component={MenuPage}></Route>
        <Route exact path="/menupage/javascript" component={JavaScript}></Route>
        <Route exact path="/menupage/reactpages" component={ReactPages}></Route>
        <Route exact path="/menupage/reactpages/calculator" component={Calculator}></Route>
        <Route exact path="/menupage/node" component={Node}></Route>
        <Route exact path="/menupage/pub" component={Pub}></Route>
      </Router>
    </>
  );
}

export default App;
