import React from 'react'
import styled from 'styled-components';
import { Icon } from 'antd';
import { Link } from 'react-router-dom';

const FirstIcon = styled.div`
  font-size: 3.5em;
  position: absolute;
  width: auto;
  top: 2%;
  right: 2%;
  text-align: center;
  transition: 0.5s all;
  color: white;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  @media only screen and (max-width: 500px) {
    font-size: 2.2em;
    right: 5%;
  }
`;


function MenuIcon() {
    return (
        <Link to='/menupage'>
            <FirstIcon>
                <Icon type="menu" />
            </FirstIcon>
        </Link>
    )
}

export default MenuIcon
