import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

const Div = styled.div`
    color:white;
    font-size:2em;
    position:absolute;
    right:2%;
    bottom:0%;
    vertical-align:middle;
    img{
        width:2em;
        height:auto;
        box-sizing:border-box;
        
    }
    @media only screen and (max-width:600px) {
        font-size:1.3em;
        img{
            width:2.5em;
        }       
        right:5%;
        bottom:0%;
        vertical-align:middle;
    }
`

function Whether() {
    const [state, setState] = useState({})
    const COORDS = 'coords'
    const API_KEY = 'ddda1d73dbef5892b170f3fe30a9d88a'
    const saveCoords = (coordsObj) => {
        localStorage.setItem(COORDS, JSON.stringify(coordsObj))
    }

    const getWeather = (latitude, longitude) => {
        fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&units=metric&appid=${API_KEY}&lang=kr`)
            .then(function (res) { return res.json() })
            .then(function (json) {
                console.log(json)
                setState({
                    weather: json.weather[0].icon,
                    temp: json.main.temp,
                    city: json.name
                })
            })
    }
    const handleGeoSuccess = (position) => {
        const latitude = position.coords.latitude
        const longitude = position.coords.longitude
        const coordsObj = {
            latitude,
            longitude
        }
        saveCoords(coordsObj)
        getWeather(latitude, longitude)
    }
    const handleGeoError = () => {
        console.error('위치 정보를 획득 실패!')
    }
    const askForCoords = () => {
        navigator.geolocation.getCurrentPosition(handleGeoSuccess, handleGeoError)
    }

    const loadCoords = () => {
        const loadedCoords = localStorage.getItem(COORDS)
        if (loadedCoords === null) {
            askForCoords()
        }
        else {
            const parsedCoords = JSON.parse(loadedCoords)
            getWeather(parsedCoords.latitude, parsedCoords.longitude)
        }
    }
    const init = () => {
        loadCoords()
    }

    useEffect(() => { init() }, [])

    return (
        <Div>
            {state.temp}°C , {state.city} [<img src={`http://openweathermap.org/img/wn/${state.weather}@2x.png`} alt="weather icon" />]
        </Div>
    )
}

export default Whether
