import React from 'react'
import { Button } from 'antd';
import Slider from 'react-slick';
import styled from 'styled-components';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Link } from 'react-router-dom';

const imageMapEtc = [
    {
        imgId: 1,
        title: 'ChristMas?',
        description1: 'mainPage',
        description2: '2020',
        description3: 'Vanilla Javascript',
        imgSrc: './slider2/christmas.png',
        pageHref: './javascript/christmas/index.html',
    },
    {
        imgId: 2,
        title: 'Calculator',
        description1: 'mainPage',
        description2: '2020',
        description3: 'Vanilla Javascript',
        imgSrc: './slider2/calculator.png',
        pageHref: './javascript/calculator/index.html',
    },
    {
        imgId: 3,
        title: 'Casino',
        description1: 'mainPage',
        description2: '2020',
        description3: 'Vanilla Javascript',
        imgSrc: './slider2/casino.png',
        pageHref: './javascript/casino/index.html',
    },
    {
        imgId: 4,
        title: 'Todolist',
        description1: 'mainPage',
        description2: '2020',
        description3: 'Vanilla Javascript',
        imgSrc: './slider2/todo.png',
        pageHref: './javascript/todolist/index.html',
    },
    {
        imgId: 5,
        title: 'Selectbox',
        description1: 'mainPage',
        description2: '2020',
        description3: 'Vanilla Javascript',
        imgSrc: './slider2/select.png',
        pageHref: './javascript/selectbox/index.html',
    },
];

const CarouselDiv = styled.div`
  position: relative;
  max-width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  a{position:relative;
  z-index:55}
  h2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1em;
  }
  .slick-dots li button:before {
    color: white;
    top:20px;
  }
  .slick-slider{
    max-width:1200px;
    margin: 2% auto;}

  @media only screen and (max-width:1200px){
    .slick-prev:before {
        display:none
    }   
    .slick-next:before {
        display:none
    }
    .slick-slider{
        margin: 10% auto;
        max-width: 90%;
    }
    .slick-slide{
        opacity:0.2;
    }
    .slick-slide.slick-active{
        opacity:1
        }
    }
`;

function Carousel() {
    const settings = {
        slidesToShow: 1,
        dots: true,
        infinite: true,
        autoplay: true,
        slidesToScroll: 1,
        swipe: true,
        autoplaySpeed: 1500,
        responsive: [{
            breakpoint: 800,
            settings: {
                vertical: true,
                verticalSwiping: true,
                slidesToShow: 2,
                autoplay: false,
                dots: false,
                arrow: false
            }
        }]
    };

    return (
        <CarouselDiv>
            <Link to='/menupage'><Button
                style={{
                    fontSize: '2em',
                    lineHeight: '63px',
                    height: '63px',
                    color: 'tomato',
                }}
                block
                type="normal"
            >
                Close
        </Button></Link>
            <Slider
                {...settings}
            >
                {imageMapEtc.map(i => (
                    <div style={{ position: 'relative' }} key={i.imgId}>
                        <a href={i.pageHref} target="_blank" rel="noreferrer noopener" >
                            <h2
                                style={{
                                    color: 'white',
                                    cursor: 'pointer',
                                }}
                            >
                                Title : {i.title} || {i.description1} || {i.description2} ||{' '}
                                {i.description3}
                            </h2>

                            <img
                                style={{
                                    width: '100%',
                                    maxHeight: '75vh',
                                    boxSizing: 'border-box',
                                    verticalAlign: 'middle',
                                    margin: '20px auto',
                                    top: 0,
                                    right: 0,
                                    left: 0,
                                    bottom: 0,
                                }}
                                src={i.imgSrc}
                                alt={i.title}
                            />
                        </a>
                    </div>
                ))}
            </Slider>
        </CarouselDiv>
    )
}

export default Carousel

