import React from 'react';
import styled from 'styled-components';
import { Button } from 'antd';
import { Link } from 'react-router-dom';

const ReactDiv = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  position: relative;
  transition: 0.7s all;
  h2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1em;
  }
  article {
    background: white;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 70vh;
    h1 {
      font-size: 2em;
      text-align: center;
      line-height: 2;
      padding: 30px 0 0 0;
    }
  }
`;

function Node() {
    return (
        <div>
            <ReactDiv>
                <article>

                    <a href="https://matube.herokuapp.com" target="_blank" rel="nonrefferer noopener"><h1>JULUBE-Matube!</h1></a>

                </article>
                <Link to='/menupage'>
                    <Button
                        style={{
                            fontSize: '2em',
                            lineHeight: '63px',
                            height: '63px',
                            position: 'absolute',
                            top: 0,
                            borderRadius: 0,
                            color: 'tomato',
                        }}
                        block
                        type="normal"
                    >
                        Close
                    </Button>
                </Link>
            </ReactDiv>
        </div>
    )
}

export default Node
